function addControlPanel() {

  setTimeout(() => {
    const icon = `<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" x="0px" y="0px" viewBox="0 0 512 512" style="enable-background:new 0 0 512 512;" xml:space="preserve" width="17" height="17"><g><path d="M34.283,384c17.646,30.626,56.779,41.148,87.405,23.502c0.021-0.012,0.041-0.024,0.062-0.036l9.493-5.483   c17.92,15.332,38.518,27.222,60.757,35.072V448c0,35.346,28.654,64,64,64s64-28.654,64-64v-10.944   c22.242-7.863,42.841-19.767,60.757-35.115l9.536,5.504c30.633,17.673,69.794,7.167,87.467-23.467   c17.673-30.633,7.167-69.794-23.467-87.467l0,0l-9.472-5.461c4.264-23.201,4.264-46.985,0-70.187l9.472-5.461   c30.633-17.673,41.14-56.833,23.467-87.467c-17.673-30.633-56.833-41.14-87.467-23.467l-9.493,5.483   C362.862,94.638,342.25,82.77,320,74.944V64c0-35.346-28.654-64-64-64s-64,28.654-64,64v10.944   c-22.242,7.863-42.841,19.767-60.757,35.115l-9.536-5.525C91.073,86.86,51.913,97.367,34.24,128s-7.167,69.794,23.467,87.467l0,0   l9.472,5.461c-4.264,23.201-4.264,46.985,0,70.187l-9.472,5.461C27.158,314.296,16.686,353.38,34.283,384z M256,170.667   c47.128,0,85.333,38.205,85.333,85.333S303.128,341.333,256,341.333S170.667,303.128,170.667,256S208.872,170.667,256,170.667z"/></g></svg>`;
    const menu = document.querySelector(".gc-account-leftbar");
    const controlPanel = document.createElement("div");

    controlPanel.classList.add("leftbar-controls");

    controlPanel.innerHTML = `
      <div class="wrapper-control">
        <div class="control-panel-label">
          <div class="controls-icon">${icon}</div>
        </div>
        <div class="controls-buttons">
          <div class="leftbar-control">
            <a href="javascript:void(0);" class="control-item size-plus">A+</a>
          </div>
          <div class="leftbar-control">
            <a href="javascript:void(0);" class="control-item size-minus">A-</a>
          </div>
        </div>
      </div>
    `;

    const plusButton = controlPanel.querySelector(".size-plus");
    const minusButton = controlPanel.querySelector(".size-minus");

    plusButton.addEventListener("click", () => changeFontSize(2));
    minusButton.addEventListener("click", () => changeFontSize(-2));

    function changeFontSize(count) {
      const body = document.querySelector("body");
      const currentFontSize = parseInt(window.getComputedStyle(body).fontSize);
      const newSize = currentFontSize + count;

      if (newSize >= 12 && newSize <= 24) {
        body.style.fontSize = newSize + "px";
        localStorage.setItem("fontSize", newSize);
      }
    }

    const savedFontSize = localStorage.getItem("fontSize");

    if (savedFontSize) {
      document.querySelector("body").style.fontSize = savedFontSize + "px";
    }

    if (window.innerWidth <= 768) {
      const mobileWrapper = document.querySelector(".mobile-wrapper");
      mobileWrapper.appendChild(controlPanel);
    } else {
      menu.appendChild(controlPanel);
    }
  }, 200);
}
document.addEventListener("DOMContentLoaded", addControlPanel);